.subscribe-btn {
  padding: 0.8rem 2rem;
  border-radius: 4px;
  background: linear-gradient(90deg, #ff9966, #ff5e62);
  color: #fff;
  font-size: 0.7rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin-left: 20px;
  transition: all 0.5s;
  cursor: pointer;
  width: 30vh;
}

.subscribe-btn a {
  text-decoration: none;
  color: #fff;
}

.subscribe-btn span {
  cursor: pointer;
  display: inline;
  position: relative;
  transition: 0.5s;
}

.subscribe-btn span:after {
  content: "\00bb";
  position: relative;
  opacity: 0;
  top: 0;
  left: -15px;
  transition: 0.5s;
}

.subscribe-btn:hover span {
  padding-left: 25px;
  padding-bottom: 0;
}

.subscribe-btn:hover a {
  color: whitesmoke;
  opacity: 1;
  right: 0;
}

.subscribe-btn:hover span:after {
  opacity: 1;
  right: 0;
}

